.container {
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    @media screen and (min-width: 768px) {
        max-width: 688px;
        padding-left: 0;
        padding-right: 0;
    }
    @media screen and (min-width: 1366px) {
        max-width: 1314px;
    }
}
